import api from '../../utils/api.js';
import util from '../../utils/util.js';
export default {
	data() {
		return {
			school: '',
			grade: '',
			allNum: 0,
			list: [],
			page: 0,
			isNoMore: false,
			isLoading: false
		};
	},
	onLoad() {
		const that = this;
		api.index({
			success: function(res){
				that.school = res.school;
				that.grade = res.grade;
			}
		})
		this.getListData();
	},
	// 上拉触底
	onReachBottom() {
		this.getListData();
	},
	methods: {
		getListData(){
			const that = this;
			if(this.isNoMore || this.isLoading){
				return false;
			}
			let page = this.page + 1;
			let size = 10;
			api.photos({
				data:{
					type: 2,
					page: page,
					size: size
				},
				success: function(res){
					that.allNum = res.count;
					let list = res.list;
					if (list.length < size) {
						that.isNoMore = true;
					}
					that.list = that.list.concat(list);
					that.page = page;
				},
				fail: function(res){
					that.isNoMore = true;
				},
				complete: function(res){
					that.isLoading = false;
				}
			})
		}
	}
};
